import React from 'react';
import firebase from 'firebase'
class Post extends React.Component {
    state = { posts: [] };

    componentDidMount() {
        firebase
          .firestore()
          .collection("posts")
          .get()
          .then(querySnapshot => {
            const posts = [];
    
            querySnapshot.forEach(function(doc) {
              Matches.push({
                topic: doc.data().team1,
                subject: doc.data().team2,
              });
            });
    
            this.setState({ posts });
          })
          .catch(function(error) {
            console.log("Error getting documents: ", error);
          });
      }

      render() {
        return (
          <div id="against">
            {this.state.Matches.map(v => {
              return (
                <p>
                  Team1: {v.team1},
                  Team2: {v.team2},
                  Winner: {v.winner},
                  Date: {v.date}
                </p>
              );
            })}
          </div>
        );
      }
}
export default Post;